.container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  gap: 40px;
  margin-top: 40px;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  background-color: var(--bg-primary);
  color: var(--text-primary);
  transition: all 0.3s ease;
}

.game-area {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 340px;
}

.board {
  display: grid;
  grid-template-columns: repeat(3, 100px);
  gap: 5px;
  justify-content: center;
  margin: 20px auto;
}

.sidebar {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 200px;
  margin-top: 20px;
}

.sidebar input {
  padding: 8px;
  font-size: 1rem;
  background-color: var(--card-bg);
  color: var(--text-primary);
  border: 1px solid var(--border-color);
  border-radius: 5px;
  transition: all 0.3s ease;
}

.sidebar button {
  padding: 8px 12px;
  background-color: var(--accent-primary);
  color: white;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.sidebar button:hover {
  background-color: var(--accent-secondary);
}
.square {
  width: 100px;
  height: 100px;
  font-size: 2rem;
  font-weight: bold;
  border: 2px solid #333;
  border-radius: 8px;
  cursor: pointer;
  background-color: var(--card-bg);
  border: 2px solid var(--border-color);
  color: var(--text-primary);
  transition: all 0.3s ease;
}

.square.x {
  color: var(--x-color);
}

.square.o {
  color: var(--o-color);
}

.square:hover {
  background-color: var(--square-hover);
}

.status {
  font-size: 1.2rem;
  margin: 20px auto;
  text-align: center;
  min-height: 3rem;
}

.winner {
  font-size: 1.5rem;
  animation: pop 0.3s ease-in-out;
}

.x-winner {
  font-size: 2.5rem;
  color: #007bff;
}

.o-winner {
  font-size: 2.5rem;
  color: #dc3545;
}

.highlight {
  background-color: var(--highlight-bg) !important;
  border: 2px solid var(--highlight-border);
  animation: glow 0.6s ease-in-out infinite alternate;
}

@keyframes glow {
  from {
    box-shadow: 0 0 5px var(--highlight-border);
  }
  to {
    box-shadow: 0 0 15px var(--highlight-border);
  }
}
@keyframes pop {
  0% {
    transform: scale(0.9);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.restart {
  padding: 10px 20px;
  font-size: 1rem;
  background-color: var(--accent-primary);
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.restart:hover {
  background-color: var(--accent-secondary);
}
